  headquarter_location: "",
  industry_type: {
    industry_no: "",
    industry_nm: "",
  },
  contract_status: [],
  quotation_request_result: [],
  subsidy_assessment_result: [],
  processor_name: "",
  company_size: [],
  employee_count_start: "",
  employee_count_end: "",
  capital_start: "",
  capital_end: "",
  sales_amount_start: "",
  sales_amount_end: "",
  area_start: "",
  area_end: "",
  no_of_store_start: "",
  no_of_store_end: "",
  searchFilter: {
    reduction_proposal_name: "",
    supplier_reduction_proposal_info_no: "",
  },
  registered_expense_item: {
    expense_item_nm: "",
    accounts_name_cd: "",
    expense_item_cd: "",
  },
  processor_name_list: [],
};

export default {
  name: "ClientList",
  components: { LTable, SubMenuTree },
  data() {
    return {
      inputFilter: {},
      treeOptions: {},
      treeData: [],
      allTreeData: [],
      allIndustryTreeData: [],
      headquarterLocationOptions: [],
      showFirst: true,
      showSecond: false,
      sortByDesc: true,
      isBusy: false,
      total: 0,
      limit: 50,
      start: 0,
      currentPage: 1,
      tableData: [],
      tableExpenseData: [],
      pageOptions: [50, 100, 200],
      processorName: "",
      processerNameNotSet: this.$t("PROCESSERNAMENOTSET"),
      searchFilter: null,
      prefectureLoading: false,
      currentSelectedIndustryItem: "",
      processorNameOptions: [],
      treeOptionsExpenseItem: {
        checkbox: false,
      },
      addExpenseTitle: "",
      expense_code: { expense_item_cd: "", accounts_name_cd: "" },
      expenseItemOptions: [],
      checkedItems: "",
      checkedClientItems: [],
      clientArray: [],
    };
  },
  validations: {
    inputFilter: {
      company_name: {},
      company_representative_name: {},
      headquarter_location: {},
      industry_type: {
        industry_no: "",
        industry_nm: "",
      },
      searchFilter: {},
      contract_status: {},
      quotation_request_result: {},
      subsidy_assessment_result: {},
      processor_name: {},
      company_size: {},
      employee_count_start: {},
      employee_count_end: {},
      capital_start: {},
      capital_end: {},
      sales_amount_start: {},
      sales_amount_end: {},
      area_start: {},
      area_end: {},
      no_of_store_start: {},
      no_of_store_end: {},
      registered_expense_item: {
        expense_item_nm: {},
        accounts_name_cd: {},
        expense_item_cd: {},
      },
      processor_name_list: {},
    },
  },
  computed: {
    ...mapGetters({
      selected_industry_item: "getSelectedIndustryItem",
      selected_expense_item: "getSelectedExpenseItem",

      client_details: "getClientData",
    }),
    fields() {
      return [
        {
          key: "selected",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "client_name",
          label: this.$t("COMPANYSTORENAME"),
          class: "text-left normal-width",
        },
        {
          key: "industry_type",
          label: this.$t("INDUSTRY"),
          class: "text-left normal-width",
        },
        {
          key: "prefecture",
          label: this.$t("PREFECTURES"),
          class: "text-center normal-width",
        },
        {
          key: "quotation_count",
          label: this.$t("REQUESTFORQUOTATION"),
          class: "text-center normal-width",
        },
        {
          key: "subsidy_assessment",
          label: this.$t("SUBSIDYASSESSMENT"),
          class: "text-center  normal-width",
        },
        {
          key: "create_dt",
          label: this.$t("USERSTARTDATE"),
          class: "text-center normal-width",
        },
        {
          key: "contract_status",
          label: this.$t("CONTRACTSTATUS"),
          class: "text-center normal-width ",
        },
        {
          key: "person_name",
          label: this.$t("PROCESSORNAME"),
          class: "text-center normal-width",
        },
      ];
    },
    expenseFields() {
      return [
        {
          key: "checked",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "expense_item_name",
          label: this.$t("EXPENSEITEM"),
          class: "text-center normal-width",
        },
        {
          key: "reduction_proposal_name",
          label: this.$t("REDUCTIONPROPOSALMENUNAME"),
          class: "text-center normal-width",
        },

        {
          key: "created_date",
          label: this.$t("EXPIRATIONDATESTART"),
          class: "text-center normal-width",
        },
        {
          key: "updated_date",
          label: this.$t("EXPIRATIONDATEEND"),
          class: "text-center  normal-width",
        },
        {
          key: "reduction_proposal_cost",
          label: this.$t("PROPOSEDAMOUNT"),
          class: "text-center normal-width",
        },
      ];
    },
    totalRows() {
      if (this.total) {
        return this.total;
      }
      return this.getRowCount();
    },
    items() {
      return this.tableData;
    },
    contractOptions() {
      return [
        { text: i18n.t("ALL"), value: "0" },
        { text: i18n.t("NOCONTRACT"), value: "1" },
        { text: i18n.t("UNDERCONTRACT"), value: "2" },
        { text: i18n.t("ORDERRECEIVED"), value: "3" },
      ];
    },
    quotationResultOptions() {
      return [
        { text: i18n.t("ALL"), value: "0" },
        { text: i18n.t("NONE"), value: "1" },
        { text: i18n.t("YES2"), value: "2" },
      ];
    },
    subsidyAssessmentOptions() {
      return [
        { text: i18n.t("ALL"), value: "0" },
        { text: i18n.t("NONE"), value: "1" },
        { text: i18n.t("YES2"), value: "2" },
      ];
    },
    companySizeOptions() {
      return [
        { text: i18n.t("ALL"), value: "0" },
        { text: i18n.t("SMALLBUSINESS"), value: "1" },
        { text: i18n.t("MEDIUMSCALEBUSINESS"), value: "2" },
        { text: i18n.t("LARGESCALEBUSINESS"), value: "3" },
      ];
    },
    processorNameListOptions() {
      return [
        { text: i18n.t("ALL"), value: "0" },
        { text: i18n.t("NOCONTRACT"), value: "1" },
        { text: i18n.t("UNDERCONTRACT"), value: "2" },
      ];
    },
  },
  watch: {
    currentPage() {
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
    "inputFilter.contract_status": function (newVal, oldVal) {
      if (this._.isEqual(newVal, oldVal)) {
        return;
      }
      console.log("OOOOOO", newVal);
      if (newVal[newVal.length - 1] == 0) {
        this.inputFilter.contract_status = ["0"];
      } else {
        this.inputFilter.contract_status = this._.filter(
          this.inputFilter.contract_status,
          (item) => {
            return item != "0";
          }
        );
      }
    },
    "inputFilter.quotation_request_result": function (newVal, oldVal) {
      if (this._.isEqual(newVal, oldVal)) {
        return;
      }
      if (newVal[newVal.length - 1] == 0) {
        this.inputFilter.quotation_request_result = ["0"];
      } else {
        this.inputFilter.quotation_request_result = this._.filter(
          this.inputFilter.quotation_request_result,
          (item) => {
            return item != "0";
          }
        );
      }
    },
    "inputFilter.subsidy_assessment_result": function (newVal, oldVal) {
      if (this._.isEqual(newVal, oldVal)) {
        return;
      }
      if (newVal[newVal.length - 1] == 0) {
        this.inputFilter.subsidy_assessment_result = ["0"];
      } else {
        this.inputFilter.subsidy_assessment_result = this._.filter(
          this.inputFilter.subsidy_assessment_result,
          (item) => {
            return item != "0";
          }
        );
      }
    },
    "inputFilter.company_size": function (newVal, oldVal) {
      if (this._.isEqual(newVal, oldVal)) {
        return;
      }
      if (newVal[newVal.length - 1] == 0) {
        this.inputFilter.company_size = ["0"];
      } else {
        this.inputFilter.company_size = this._.filter(
          this.inputFilter.company_size,
          (item) => {
            return item != "0";
          }
        );
      }
    },
    "inputFilter.processor_name_list": function (newVal, oldVal) {
      if (this._.isEqual(newVal, oldVal)) {
        return;
      }
      if (newVal[newVal.length - 1] == 0) {
        this.inputFilter.processor_name_list = ["0"];
      } else {
        this.inputFilter.processor_name_list = this._.filter(
          this.inputFilter.processor_name_list,
          (item) => {
            return item != "0";
          }
        );
      }
    },
  },
  mounted() {
    this.getExpenseEvaluateData();
    this.getPrefectureList();
    this.getTreeData();
    this.getIndustryTreeData();
    this.getPersonListByAuth();
  },
  created() {
    this.inputFilter = { ...defaultForm };
  },
  methods: {
    /**
     * @description function to check the authorization
     * @name getContetLevelAccess
     */
    getContentLevelAccess(accessID) {
      let checkData = JSON.parse(localStorage.getItem("dummyData"));
      const foundAccess = checkData ? checkData.hasOwnProperty(accessID) : true;
      if (foundAccess) {
        return false;
      } else {
        return true;
      }
    },
    goToClientRegistration(){
      this.$router.push({ name: "create_client" });
    },
    /**
     * @description function to fetch the  personName based on the personId or personCompany
     * @name getpersonListByAuth
     */
    async getPersonListByAuth() {
      let personAuth = localStorage.getItem("person_auth");
      let personID = localStorage.getItem("personID");
      let personCompany = localStorage.getItem("personCompanyID");
      if (personAuth == "01") {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.MASTERREADMPERSON,
          {
            fieldlist: ["person_id", "person_name"],
            conditions: [
              {
                key: "person_id",
                value: personID,
              },
            ],
          }
        );
        if (data.success) {
          let responseData = data.data;
          this.processorNameOptions = responseData;
        } else {
          this.processorNameOptions = [];
        }
      } else {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.MASTERREADMPERSON,
          {
            fieldlist: ["person_id", "person_name"],
            conditions: [
              {
                key: "person_company",
                value: personCompany,
              },
            ],
          }
        );
        if (data.success) {
          let responseData = data.data;
          this.processorNameOptions = responseData;
        } else {
          this.processorNameOptions = [];
        }
      }
    },
    /**
     * @description function to show to pop-up modal
     * @name showProposalModal
     */
    showProposalModal() {
      this.checkedItems = "";
      (this.expense_code = { expense_item_cd: "", accounts_name_cd: "" }),
        this.getExpenseData();
      this.getExpenseItemList();
      this.$refs["proposal-modal"].show();
    },
    /**
     * @description function to hide the modal
     * @name hideMessageModal
     */
    hideMessageModal() {
      this.$refs["proposal-modal"].hide();
    },
    /**
     * @description function to fetch the expenseData based on the change of expense
     * @name onExpenseChange
     */
    onExpenseChange() {
      this.getExpenseData();
    },
    /**
     * @description function to fetch selected checked item in expense popup modal
     * @name selectItem
     */
    selectItem(items) {
      this.checkedItems = items;
      console.log("checkedItems", items);
    },
    /**
     * @description function to fetch selected checked item in client list
     * @name selectClient
     */
    selectClient(clients) {
      this.checkedClientItems = clients;
      this.clientArray = [];
      this.checkedClientItems.forEach((item) => {
        let selectedRow = this._.find(this.tableData, {
          id: item,
        });
        this.clientArray.push(selectedRow.client_no);
      });

      console.log("clientArray", this.clientArray);
    },
    /**
     * @description function to hide the  popup modal
     * @name selectItem
     */
    addExpenseFilter() {
      console.log("checkedItems", this.checkedItems);
      let selectedRow = this.checkedItems.item;
      console.log(
        "selectedRow",
        selectedRow,
        selectedRow.reduction_proposal_name,
        selectedRow.id.substring(7)
      );
      this.inputFilter.searchFilter = {
        reduction_proposal_name: selectedRow.reduction_proposal_name,
        supplier_reduction_proposal_info_no: selectedRow.id.substring(7),
      };
      this.hideMessageModal();
    },
    /**
     * @description function to fetch the expense name and set for the filter
     * @name getExpenseData
     */
    async getExpenseData() {
      this.isBusy = true;
      let compiledArray;
      if (this.expense_code.expense_item_cd == "") {
        compiledArray = {
          expense_item_cd: this.expense_code.expense_item_cd,
          accounts_name_cd: this.expense_code.accounts_name_cd,
          person_id: "",
        };
      } else {
        compiledArray = {
          expense_item_cd: this.expense_code.expense_item_cd,
          accounts_name_cd: this.expense_code.accounts_name_cd,
          person_id: localStorage.getItem("personID"),
        };
      }

      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADSUPPLIERREDUCTIONPROPOSAL,
        compiledArray
      );
      this.isBusy = false;

      if (data.success) {
        let listObject = data.data.supplierReductionProposalInfoListObject;

        let mapData = [];
        listObject.forEach((item) => {
          let listData = {
            id: "expense" + item.supplier_expense_item_info_no,
            expense_item_name: item.expense_item_nm,
            reduction_proposal_name: item.reduction_proposal_name,
            reduction_proposal_cost: this.filterByCurrency(
              item.reduction_proposal_cost
            ),

            created_date: getDate(new Date(item.effective_date_st)),
            updated_date: getDate(new Date(item.effective_date_ed)),
          };
          mapData.push(listData);
        });
        this.tableExpenseData = mapData;
      }
    },
    /**
     * @description function to fetch the expense details for the expense popup
     * @name getExpenseItemList
     */
    async getExpenseItemList() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.SUPPLIERREADSUPPLIEREXPENSEITEMLIST,
        {
          person_id: localStorage.getItem("personID"),
        }
      );
      if (data.success) {
        let responseData = data.data.expenseItemObjectList;
        let expenseListData = [];
        if (responseData.length) {
          responseData.forEach((item) => {
            let iterateExpenseItem = {
              text: item.expense_item_nm ? item.expense_item_nm : "",
              value: {
                expense_item_cd: item.expense_item_cd
                  ? item.expense_item_cd
                  : "",
                accounts_name_cd: item.accounts_name_cd
                  ? item.accounts_name_cd
                  : "",
              },
            };
            if (item.expense_item_nm) {
              expenseListData.push(iterateExpenseItem);
            }
          });
          this.expenseItemOptions = expenseListData;
        } else {
          this.expenseItemOptions = [];
        }
      } else {
        this.expenseItemOptions = [];
      }
    },
    /**
     * @description function to call the clientDetails page
     * @name clientClick
     */
    clientClick(item) {
      console.log(item);
      if (
        this.getContentLevelAccess("client-process-account-info-crm-16") ==
        false
      ) {
        localStorage.setItem("activeTab", 1);
      } else {
        localStorage.setItem("activeTab", 0);
      }

      this.$store.dispatch("setClientData", {
        client_name: item.item.client_name,
        client_no: item.item.client_no,
        client_icon_path: "",
      });
      this.$router.push({ name: "client_process" });
    },
    /**
     * @description function to delete the client updating the delete flag
     * @name deleteClient
     */
    async deleteClient() {
      this.$swal({
        text: i18n.t("AREYOUSUREDELETECLIENT"),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: i18n.t("YESDELETE"),
        cancelButtonText: i18n.t("NODELETE"),
      }).then(async (result) => {
        if (result.value) {
          let compiledArray = {
            client_no: this.clientArray,
            update_id : localStorage.getItem("personID")
          };

          const { data } = await BasicInfoApis.postAPI(
            endPoint.CLIENTDELETE,
            compiledArray
          );

          if (data.success) {
            this.$swal({ type: "success", text: i18n.t("DELETED") });
            this.getExpenseEvaluateData();
          } else {
            this.$swal({ type: "error", text: i18n.t("NOTDELETED") });
          }
        }
      });
    },
    /**
     * @description function to search the client list details based on the search conditions
     * @name search
     */
    search() {
      this.getExpenseEvaluateData();
    },
    /**
     * @description function to reset the form
     * @name reset
     */
    reset() {
      this.$v.$reset();
      this.inputFilter = { ...defaultForm };
      this.getExpenseEvaluateData();
    },
    getExpansionItem() {},
    /**
     * @description function to fetch the expense items and account items to show in the  expense popup
     * @name getTreeData
     */
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READWITHJOINMEXPENSEITEM,
        {
          paginate: false,
          limit: 5000,
          fieldlist: [
            "m_accounts_name.accounts_name_cd",
            "m_accounts_name.accounts_name_nm",
            "m_expense_item.expense_item_cd",
            "m_expense_item.expense_item_nm",
          ],
          conditions: [
            {
              key: "m_accounts_name.use_flg",
              value: true,
            },
          ],
          joins: [
            {
              table: "m_accounts_name",
              join_type: "inner",
              join_conditions: [
                {
                  join_using: "m_accounts_name.accounts_name_cd",
                  join_operator: "=",
                  join_with: "m_expense_item.accounts_name_cd",
                },
              ],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data.data;
        console.log("PPPPPPPPP", responseData);
        this.allTreeData = responseData;
        let mapData = [];
        let groupedData = this._.groupBy(responseData, "accounts_name_nm");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          groupedData[key].forEach((item) => {
            let bindChildData = {
              text: item.expense_item_nm,
            };
            childrenArray.push(bindChildData);
          });
          let bindTreeData = {
            text: key,
            state: { expanded: true },
            children: childrenArray,
          };
          mapData.push(bindTreeData);
        });
        this.$store.dispatch("updateTree", mapData);
      }
    },
    /**
     * @description function to show the expense popup
     * @name showExpenseModal
     */
    showExpenseModal() {
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: "",
        expense_item_cd: "",
        expense_item_nm: "",
      });
      this.$refs["tree-modal"].show();
    },
    /**
     * @description function to get the count of row length
     * @name getRowCount
     */
    getRowCount: function () {
      return this.items.length;
    },
    /**
     * @description function to fetch the privious page data
     * @name pageChange
     */
    pageChange(page) {
      this.start = page - 1;
      this.getExpenseEvaluateData();
    },
    /**
     * @description function to fetch the next page data
     * @name perPageChange
     */
    perPageChange(page) {
      this.limit = page;
      this.getExpenseEvaluateData();
    },
    expenseDetails(item) {
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: item.item.accounts_code,
        expense_item_cd: item.item.expenseItemCode,
        expense_item_nm: item.item.expense_item,
      });

      this.$router.push({ name: "monthly_expense_details" });
    },
    /**
     * @description function to add the conditions to fetch the client list data
     * @name getExpenseEvaluateData
     */
    async getExpenseEvaluateData() {
      this.isBusy = true;
      this.checkedClientItems = [];
      //where conditions
      let conditions = [];
      if (this.inputFilter.company_name) {
        conditions.push({
          key: "m_client_base_info.client_name",
          value: this.inputFilter.company_name,
        });
      }
      if (this.inputFilter.company_representative_name) {
        conditions.push({
          key: "m_client_base_info.client_person_name",
          value: this.inputFilter.company_representative_name,
        });
      }
      if (this.inputFilter.headquarter_location) {
        conditions.push({
          key: "m_client_base_info.client_main_office_kbn",
          value: this.inputFilter.headquarter_location,
        });
      }

      if (this.inputFilter.industry_type.industry_nm) {
        conditions.push({
          key: "m_client_company_size_info.industry_no",
          value: this.inputFilter.industry_type.industry_no,
        });
      }

      if (this.inputFilter.contract_status.length) {
        conditions.push({
          key: "t_agreement.contract",
          value: this.inputFilter.contract_status,
        });
      }
      if (this.inputFilter.processor_name) {
        conditions.push({
          key: "t_processor.person_id",
          value: this.inputFilter.processor_name,
        });
      }

      if (this.inputFilter.quotation_request_result.length) {
        conditions.push({
          key: "t_request_quotation_count.quotation_count",
          value: this.inputFilter.quotation_request_result,
        });
      }

      if (this.inputFilter.subsidy_assessment_result.length) {
        conditions.push({
          key: "t_subsidy_assessment_count.assessment_count",
          value: this.inputFilter.subsidy_assessment_result,
        });
      }

      if (this.inputFilter.processor_name_list.length) {
        conditions.push({
          key: "m_client_other_info.input_agency_option",
          value: this.inputFilter.processor_name_list,
        });
      }

      if (this.inputFilter.company_size.length) {
        conditions.push({
          key: "m_client_company_size_info.company_size",
          value: this.inputFilter.company_size,
        });
      }

      if (this.inputFilter.searchFilter.reduction_proposal_name) {
        conditions.push({
          key: "t_reduction_proposal.supplier_reduction_proposal_info_no",
          value: this.inputFilter.searchFilter
            .supplier_reduction_proposal_info_no,
        });
      }

      if (this.inputFilter.registered_expense_item.accounts_name_cd) {
        conditions.push({
          key: "t_cost_info.accounts_name_cd",
          value: this.inputFilter.registered_expense_item.accounts_name_cd,
        });
      }

      if (this.inputFilter.registered_expense_item.expense_item_cd) {
        conditions.push({
          key: "t_cost_info.expense_item_cd",
          value: this.inputFilter.registered_expense_item.expense_item_cd,
        });
      }

      //between conditions
      let betweenConditions = [];
      if (
        this.inputFilter.employee_count_start &&
        this.inputFilter.employee_count_end
      ) {
        betweenConditions.push({
          key: "m_client_company_size_info.employee_number",
          start_value: this.inputFilter.employee_count_start,
          end_value: this.inputFilter.employee_count_end,
        });
      }
      if (this.inputFilter.capital_start && this.inputFilter.capital_end) {
        betweenConditions.push({
          key: "m_client_company_size_info.capital",
          start_value: this.inputFilter.capital_start,
          end_value: this.inputFilter.capital_end,
        });
      }
      if (
        this.inputFilter.sales_amount_start &&
        this.inputFilter.sales_amount_end
      ) {
        betweenConditions.push({
          key: "m_client_company_size_info.revenue",
          start_value: this.inputFilter.sales_amount_start,
          end_value: this.inputFilter.sales_amount_end,
        });
      }

      if (this.inputFilter.area_start && this.inputFilter.area_end) {
        betweenConditions.push({
          key: "m_client_company_size_info.floor_space",
          start_value: this.inputFilter.area_start,
          end_value: this.inputFilter.area_end,
        });
      }

      if (
        this.inputFilter.no_of_store_start &&
        this.inputFilter.no_of_store_end
      ) {
        betweenConditions.push({
          key: "m_client_company_size_info.number_of_stores",
          start_value: this.inputFilter.no_of_store_start,
          end_value: this.inputFilter.no_of_store_end,
        });
      }

      let compiledArray = {
        conditions: conditions,
        betweenConditions: betweenConditions,
        limit: this.limit,
        offset: this.start * this.limit,
      };
      let valueCheckArray = [];
      if (
        this.inputFilter.employee_count_start ||
        this.inputFilter.employee_count_end
      ) {
        let end = this.inputFilter.employee_count_end
          ? this.inputFilter.employee_count_end
          : 0;

        parseInt(this.inputFilter.employee_count_start) > parseInt(end)
          ? valueCheckArray.push(this.$t("EMPLOYEECOUNT"))
          : "";
      }
      if (this.inputFilter.capital_start || this.inputFilter.capital_end) {
        let end = this.inputFilter.capital_end
          ? this.inputFilter.capital_end
          : 0;

        parseInt(this.inputFilter.capital_start) > parseInt(end)
          ? valueCheckArray.push(this.$t("CAPITAL"))
          : "";
      }
      if (
        this.inputFilter.sales_amount_start ||
        this.inputFilter.sales_amount_end
      ) {
        let end = this.inputFilter.sales_amount_end
          ? this.inputFilter.sales_amount_end
          : 0;

        parseInt(this.inputFilter.sales_amount_start) > parseInt(end)
          ? valueCheckArray.push(this.$t("SALESAMOUNT"))
          : "";
      }
      if (this.inputFilter.area_start || this.inputFilter.area_end) {
        let end = this.inputFilter.area_end ? this.inputFilter.area_end : 0;

        parseInt(this.inputFilter.area_start) > parseInt(end)
          ? valueCheckArray.push(this.$t("AREA"))
          : "";
      }
      if (
        this.inputFilter.no_of_store_start ||
        this.inputFilter.no_of_store_end
      ) {
        let end = this.inputFilter.no_of_store_end
          ? this.inputFilter.no_of_store_end
          : 0;

        this.inputFilter.no_of_store_start > parseInt(end)
          ? valueCheckArray.push(this.$t("NOOFSTORES"))
          : "";
      }
      let errorMessage = this.$t("RANGEISSUE");
      valueCheckArray.forEach((item) => {
        errorMessage += `<br />` + item;
      });
      errorMessage = `<p class="text-left">` + errorMessage + `</p>`;
      if (valueCheckArray.length > 0) {
        this.$swal({
          width: 500,
          type: "warning",
          html: errorMessage,
        });
        this.isBusy = false;
      } else {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.CLIENTLIST,
          compiledArray
        );
        this.isBusy = false;
        this.total = 0;

        if (data.success) {
          let responseData = data.data.ClientObjectList;
          let mapData = [];
          this.total = data.data.total;
          // console.log("KKKKKKKKKK", data.data);
          responseData.forEach((item, index) => {
            let listData = {
              id: index,
              client_no: item.client_no,
              client_name: item.client_name,
              industry_type: item.industry_nm,
              prefecture: item.value_string_small,
              quotation_count:
                Number(item.quotation_count) > 0
                  ? i18n.t("YES2")
                  : i18n.t("NONE"),
              subsidy_assessment:
                Number(item.assessment_count) > 0
                  ? i18n.t("YES2")
                  : i18n.t("NONE"),
              create_dt: getDateHour(new Date(item.create_dt)),
              contract_status: this.getContractStatus(item.contract),
              person_name: item.person_name
                ? item.person_name
                : this.processerNameNotSet,
            };
            mapData.push(listData);
          });
          this.tableData = mapData;
        }
      }
    },
    /**
     * @description function to fetch the office address data
     * @name getExpenseData
     */
    async getPrefectureList() {
      this.prefectureLoading = true;
      let compiledArray = {
        fieldlist: ["code", "value_string_small"],
        conditions: [
          {
            key: "m_general_purpose.key",
            value: "PREF",
          },
        ],
      };
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READMGENERALPURPOSE,
        compiledArray
      );
      this.prefectureLoading = false;

      if (data.success) {
        let responseData = data.data;
        this.headquarterLocationOptions = responseData;
      }
    },
    /**
     * @description function to dispay the contractStatus options
     * @name getContractStatus
     */
    getContractStatus(status) {
      let contract_status = "";
      switch (status) {
        case "1":
          contract_status = i18n.t("NOCONTRACT"); //no contract
          break;
        case "2":
          contract_status = i18n.t("UNDERCONTRACT"); //order received
          break;
        case "3":
          contract_status = i18n.t("ORDERRECEIVED"); //under contract
          break;
        default:
          contract_status = "";
          break;
      }
      return contract_status;
    },
    /**
     * @description function to show the industry popup modal
     * @name addIndustryNo
     */
    addIndustryNo() {
      this.currentSelectedIndustryItem = "";
      this.$refs["tree-modal1"].show();
    },
    /**
     * @description function to fetch the industryDetails to show in the pop up modal
     * @name getIndustryTreeData
     */
    async getIndustryTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADINDUSTRY,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "industry_kbn",
              value: ["01", "02"],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.allIndustryTreeData = responseData;
        let mapData = [];
        let sortData = this._.sortBy(responseData, "industry_big_cd");
        let groupedData = this._.groupBy(sortData, "industry_big_cd");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          let ParentData = [];
          groupedData[key].forEach((item) => {
            //industry_kbn 1 is as parent node 2 is child node data
            if (item.industry_kbn == 1) {
              let parentNode = item.industry_nm;
              ParentData.push(parentNode);
            } else {
              let bindChildData = {
                text: item.industry_nm,
              };
              childrenArray.push(bindChildData);
            }
          });
          let name = ":" + ParentData[0];
          if (key == "A") {
            let bindTreeData = {
              text: key + name,
              state: { expanded: true },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          } else {
            let bindTreeData = {
              text: key + name,
              state: { expanded: false },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          }
        });

        this.$store.dispatch("updateIndustryTree", mapData);
      }
    },
    /**
     * @description function to update  selected Industry Item  to the store
     * @name updateIndustryItem
     */
    updateIndustryItem(item) {
      let Kbn2Data = [];
      this.allIndustryTreeData.forEach((item1) => {
        if (item1.industry_kbn == 2) {
          Kbn2Data.push(item1);
        }
      });

      let treeItem = this._.find(Kbn2Data, { industry_nm: item });
      console.log(treeItem);
      this.$store.dispatch("updateselectedIndustryItem", {
        industry_nm: treeItem.industry_nm,
        industry_no: treeItem.industry_no,
      });
      this.currentSelectedIndustryItem = treeItem.industry_nm;
    },
    /**
     * @description function to update  selected expense Item  to the store
     * @name updateExpenseItem
     */
    updateExpenseItem(item) {
      this.selectedExpenseItem = item;
      let treeItem = this._.find(this.allTreeData, { expense_item_nm: item });
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: treeItem.accounts_name_cd,
        expense_item_cd: treeItem.expense_item_cd,
        expense_item_nm: treeItem.expense_item_nm,
      });
    },
    /**
     * @description function to set the industryName
     * @name getIndustryName
     */
    getIndustryName() {
      this.currentSelectedIndustryItem = this.selected_industry_item.industry_nm;
      this.inputFilter.industry_type = {
        industry_no: this.selected_industry_item.industry_no,
        industry_nm: this.selected_industry_item.industry_nm,
      };
      this.$refs["tree-modal1"].hide();
    },
    /**
     * @description function to set the expenseItemName and hide the  pop up modal
     * @name getExpenseItemName
     */
    getExpenseItemName() {
      this.inputFilter.registered_expense_item = {
        expense_item_cd: this.selected_expense_item.expense_item_cd,
        accounts_name_cd: this.selected_expense_item.accounts_name_cd,
        expense_item_nm: this.selected_expense_item.expense_item_nm,
      };

      this.$refs["tree-modal"].hide();
    },
    /**
     * @description function to convert the number to currency
     * @name filterByCurrency
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
  },
};
</script>
